*
{
margin:0;
padding:0;
}
.App-input
{
text-align:center;
background-color:lightblue;
height:300px;
color:navy;
font-size:18px;
}
.App-input input
{
font-size:14px;
border-radius:5px;
border:1pxsolidgrey;
outline:none;
padding:10px5px;
width:200px;
}
.App-input button
{
background-color:lightskyblue;
color:white;
padding:10px20px;
border-radius:30px;
margin-top:10px;
}
nav{
background-color:rgb(153,47,153);
padding:10px0;
display:flex;
justify-content:center;
margin-top:30px;
}
nav ul{
display:flex;
list-style-type:none;
justify-content:space-between;
align-items:center;
width:40%;
color:white;
}
nav ul li{
cursor:pointer;
font-size:14px;
font-weight:bold;
transition:all0.5sease-in-out;
}
nav ul li:hover{
transform:scale(1.4);
}

.navActive
{
color:chartreuse;
}
.App-container
{
background-color:blue;
color:white;
padding:10px;
}